  }
  .hero-social ul{
    display: flex;
    list-style: none;
    margin-top: 26px;
  }
  .social-iconss{
    margin: 15px;
    cursor: pointer;
  }
  .fac{
    color: 	#214692;
  }
  .ins{
    color:  #fb3958;
  }
  .you{
    color: 	#FF0000;
  }
  .twi{
    color: #1DA1F2;
  }
  /*Hover icons*/
  .fac:hover{
    color: 	#2b467a;
    transition: all 0.3s;
  }
  .ins:hover{
    color:  #aa233a;
    transition: all 0.3s;
  }
  .you:hover{
    color: 	#af0909;
    transition: all 0.3s;
  }
  .twi:hover{
    color: #1877b3;
    transition: all 0.3s;
  }
  
  @media screen and (max-width: 1155px) {
    .hero-container > h1 {
      font-size: 70px;
    }
  }
  
  @media screen and (max-width: 780px) {
    .hero-container > h1 {
      font-size: 50px;
    }
  
    .hero-container > p {
      font-size: 30px;
      text-align: center;
    }
  
    .btn-mobile {
      display: block;
      text-decoration: none;
    }
  
    .btn {
      width: 100%;
    }
  }
  
  @media screen and (max-width: 565px) {
    .hero-container > h1 {
      font-size: 40px;
      
    }
    .hero-container > p {
      font-size: 25px;
    }

  }
  @media screen and (max-width: 555px) {
    .hero-container > h1 {
      font-size: 32px;
    }
    .hero-container > p {
      font-size: 22px;
    }
  }
